* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
}

body {
    background: #4e97d1;
}

main section {
    background: #f5f5f5;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 25px;
    width: 680px;
    border-radius: 15px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.300);
    
}
main h1 {
    font-size: 2em;
    font-weight: 500;
    text-align: center;
}

main .password-display {
    margin: 30px;
    text-align: center;
    font-size: 2em;
    color: #4e97d1;
}

main .validation-display {
    font-size: 1em;
    margin: 10px 0;
    color: #f00;
}

form div {
    margin: 10px;
}

form #noc-id {
    width: 140px;
    height: 30px;
    padding: 10px;
    font-size: 1em;
    text-align: left;
    border: none;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.600);
    border-radius: 5px;
}

form #noc-id::placeholder {
    font-size: 0.9em;
}

form #noc-id:focus {
    outline: none;
}

form .input-checkbox {
    position: relative;
    height: 20px;
    width: 20px;
    background: #fff;
    appearance: none;
    border: 1px solid;
    cursor: pointer;
}

form .input-checkbox:checked {
    background: #f5f5f5;
    border: none;
}

form .input-checkbox:checked::after {
    content: "L";
    font-size: 1.5em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg) scaleX(-1);
}

form #center-custom {
    text-align: center;
}

form button {
    background: orange;
    color: #fff;
    text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.600);
    border: none;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.600);
    height: 50px;
    width: 200px;
    border-radius: 15px;
    margin: 10px 0;
    cursor: pointer;
}

form button:hover {
    background: #ff7700;
}

footer {
    position: absolute;
    bottom: 0;
    width: 100vw;
    background-color: #f5f5f5;
}

footer > p {
    font-size: 0.8em;
    text-align: center;
    padding: 10px;
}

footer a {
    text-decoration: none;
    font-size: 1em;
}

footer a:hover::after {
    content: "🔗";
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA,gBAAgB;;AAEhB;IACI,sBAAsB;IACtB,WAAW;IACX,YAAY;IACZ,yCAAyC;IACzC,eAAe;AACnB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,aAAa;IACb,YAAY;IACZ,mBAAmB;IACnB,6CAA6C;;AAEjD;AACA;IACI,cAAc;IACd,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,cAAc;IACd,cAAc;AAClB;;AAEA;IACI,cAAc;IACd,cAAc;IACd,WAAW;AACf;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,cAAc;IACd,gBAAgB;IAChB,YAAY;IACZ,4CAA4C;IAC5C,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,yDAAyD;AAC7D;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,6CAA6C;IAC7C,YAAY;IACZ,4CAA4C;IAC5C,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,cAAc;IACd,eAAe;AACnB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,YAAY;IACZ,yBAAyB;AAC7B;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,qBAAqB;IACrB,cAAc;AAClB;;AAEA;IACI,aAAa;AACjB","sourcesContent":["@charset \"UTF-8\";\n\n* {\n    box-sizing: border-box;\n    margin: 0px;\n    padding: 0px;\n    font-family: Arial, Helvetica, sans-serif;\n    font-size: 20px;\n}\n\nbody {\n    background: #4e97d1;\n}\n\nmain section {\n    background: #f5f5f5;\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    padding: 25px;\n    width: 680px;\n    border-radius: 15px;\n    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.300);\n    \n}\nmain h1 {\n    font-size: 2em;\n    font-weight: 500;\n    text-align: center;\n}\n\nmain .password-display {\n    margin: 30px;\n    text-align: center;\n    font-size: 2em;\n    color: #4e97d1;\n}\n\nmain .validation-display {\n    font-size: 1em;\n    margin: 10px 0;\n    color: #f00;\n}\n\nform div {\n    margin: 10px;\n}\n\nform #noc